.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    padding: 15px;
}

.dialog a{
    color: white;
    text-decoration: none;
}

.dialog a.active {
    color: red;
}

.dialog a img {
    width: 50px;
    border-radius: 50%;
}

.messages {
    padding: 15px;
}

.message {
    margin-left: 50%;
}
